import React from 'react';
import Gamemap from './views/gamemap'
import Setting from './views/setting'
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state={stage:'gamemap'};
  }
  render() {
    let stageItem;
    if(this.state.stage==='gamemap')
      stageItem=<Gamemap prt={this}/>;
    if(this.state.stage==='setting')
      stageItem=<Setting prt={this}/>;
    return (
      <div>
        {stageItem}
      </div>
    );
  }
}

export default App;
