class Gamemap extends React.Component {
  constructor(props){
    super(props);
    const local_data=JSON.parse(localStorage.getItem('data'));
    this.state=local_data || {
      'dongjiao': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'duoluojie': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'guanyuan': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'maisiwei': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'qiushi': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'shitang': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'xiaojuchang': {
        hover: false,
        unlock: false,
        finish: false,
      },
      'yueyalou': {
        hover: false,
        unlock: false,
        finish: false,
      },
    };
  }
  render() {
    const that=this;
    const handle_hover=(s,newstate)=>{
      if(s){
        that.setState({
          [s]: {
            ...that.state[s],
            hover: newstate
          }
        })
      }
    }
    const image=(title)=>{
      if(!this.state[title].unlock) return imgs[title].locked;
      if(this.state[title].finish) return imgs[title].finish;
      if(this.state[title].hover) return imgs[title].hover;
      return imgs[title].unlocked;
    }
    const imageItems=images.map((x,index)=>(
      <img
        style={x.style}
        src={x.type==='building'?image(x.title):x.image}
        onClick={x.cb && x.cb.bind(this)}
        onMouseEnter={(()=>handle_hover(x.title,true))}
        onMouseLeave={(()=>handle_hover(x.title,false))}
        alt={x.title}
        key={index}
      />
    ));
    return (
      <div>
        {imageItems}
      </div>
    );
  }
}

export default Gamemap;
